
@import '../styles/_iconfont.css';
*{box-sizing:border-box
}
html{font-family:'Montserrat','Segoe UI','Microsoft Yahei',Helvetica,Arial
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,button,textarea,p,blockquote,th,td{margin:0;padding:0
}
.aBlock{display:block;width:100px;height:80px;margin:2px 0
}
.outerHeight{position:fixed;height:100vh
}
.flexCenter{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.splitLine{position:relative;text-align:center;display:block
}
.splitLine:before,.splitLine:after{content:"";width:30%;height:1px;position:absolute;top:50%
}
.splitLine:before{background:linear-gradient(to left, #d71345, #be2150, #10111e);left:2px
}
.splitLine:after{background:linear-gradient(to right, #d71345, #be2150, #10111e);right:2px
}
.sideBar{z-index:1000;background:#10111e;color:#be2150
}
.sideBar .isSelectBack{color:#fef1f5
}
.sideBar .iconSize{font-size:26px
}
.sideBar .hoverSide{box-shadow:0 0 0 0 inset
}
.sideBar .hoverSide:hover{box-shadow:0 0 4px 4px inset
}
.sideBar .menuBox{width:100px;height:80px;margin:2px 0
}
.sideBar .poin{cursor:pointer
}
.sideBar .menuShow{transform:translate3d(100px, 0, 0);transition:transform .5s
}
.sideBar .menuShowN{transform:translate3d(0, 0, 0);transition:transform .5s
}
.sideBar .transit0{opacity:1;transform:translate3d(0, 0, 0);transition:all .8s cubic-bezier(.175, .885, .52, 1.275)
}
.sideBar .transit1{opacity:1;transform:translate3d(0, 0, 0);transition:all 1s cubic-bezier(.175, .885, .52, 1.275)
}
.sideBar .transit2{opacity:1;transform:translate3d(0, 0, 0);transition:all 1.2s cubic-bezier(.175, .885, .52, 1.275)
}
.sideBar .transit3{opacity:1;transform:translate3d(0, 0, 0);transition:all 1.4s cubic-bezier(.175, .885, .52, 1.275)
}
.sideBar .transitN0,.sideBar .transitN1,.sideBar .transitN2,.sideBar .transitN3{opacity:0;transform:translate3d(-100px, 0, 0);transition:all 1s
}
.sideShow{transform:translate3d(0, 0, 0);transition:transform .5s
}
.sideShowN{transform:translate3d(-100px, 0, 0);transition:transform .5s
}


