
*{box-sizing:border-box
}
html{font-family:'Montserrat','Segoe UI','Microsoft Yahei',Helvetica,Arial
}
body,div,dl,dt,dd,ul,ol,li,h1,h2,h3,h4,h5,h6,pre,code,form,fieldset,legend,input,button,textarea,p,blockquote,th,td{margin:0;padding:0
}
.aBlock{display:block;width:100px;height:80px;margin:2px 0
}
.outerHeight{position:fixed;height:100vh
}
.flexCenter{display:-ms-flexbox;display:flex;-ms-flex-align:center;align-items:center;-ms-flex-pack:center;justify-content:center
}
.splitLine{position:relative;text-align:center;display:block
}
.splitLine:before,.splitLine:after{content:"";width:30%;height:1px;position:absolute;top:50%
}
.splitLine:before{background:linear-gradient(to left, #d71345, #be2150, #10111e);left:2px
}
.splitLine:after{background:linear-gradient(to right, #d71345, #be2150, #10111e);right:2px
}
.mainRight{transform:translate3d(100px, 0, 0);transition:transform .5s
}
.mainLeft{transform:translate3d(0, 0, 0);transition:transform .5s
}
.cut{overflow:hidden
}


